.joinus {
  background-color: #000;
  padding: 15px 0;
  &-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title, &__btn {
    font-weight: 600;
    font-size: 12px;
    line-height: 18px;
    letter-spacing: 0.06em;
    text-transform: uppercase;
    color: #fff;
  }
  &__socials {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex: 0 0 110px;
  }
  &__form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 45%;
    max-width: 510px;
    input {
      height: 32px;
      background-color: rgba(255, 255, 255, 0.2);
      padding: 7px 16px;
      font-weight: 500;
      font-size: 13px;
      line-height: 18px;
      color: #fff;
      border: none;
      outline: none;
      width: 100%;
      margin-right: 12px;
      &::placeholder {
        font-weight: 500;
        font-size: 13px;
        line-height: 18px;
        color: #fff;
        opacity: 0.4;
      }
    }
  }
  &__btn {
    line-height: 30px;
    background-color: #000;
    border: 1px solid #fff;
    padding: 0px 23px;
    min-width: 108px;
  }
}